import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const About = () =>
  import(/* webpackChunkName: "about" */ "../views/About.vue");
const ProductLessonb = () =>
  import(/* webpackChunkName: "product" */ "../views/ProductLessonb.vue");
const ProductScoredent = () =>
  import(/* webpackChunkName: "product" */ "../views/ProductScoredent.vue");
const PrivacyPolicy = () =>
  import(/* webpackChunkName: "privacypolicy" */ "../views/PrivacyPolicy.vue");

export const routes: Array<RouteConfig> = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/product/lessonb",
    name: "Lessonb",
    component: ProductLessonb,
  },
  {
    path: "/product/scoredent",
    name: "Scoredent",
    component: ProductScoredent,
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: About,
  },
  {
    path: "/privacypolicy",
    name: "Privacy policy",
    component: PrivacyPolicy,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
